import React from 'react';
import { useHistory } from "react-router-dom";

import './style.scss';


const Campaigns = ({ campanha }) => {
  const history = useHistory();

  const rotasCampanhas = [
    {
      route: 'oboticario',
    },
    {
      route: 'havaianas'
    },
    {
      route: 'doritos'
    },
    {
      route: 'bebe'
    },
    {
      route: 'rainbow'
    },
  ]

  let controleRota = false;

  rotasCampanhas.forEach(element => {
    if (element.route === campanha) {
      controleRota = true;
    }
  });

  if (controleRota === false) {
    history.push("/");
  }

  return (
    <div>
      <p className="text">{campanha} campanhas</p>
      <a href="/" className="link-home">HOME</a>
    </div >
  );
}

export default Campaigns;
